.cart-elem {
	display: flex;

	&__img {
		flex: 0 0 auto;
		align-self: flex-start;
		width: 150px;

		// height: 150px
		border-radius: 20px;
		background-color: rgba(#cdc8e3, 0.4);
		display: flex;
		align-items: center;
		justify-content: center;
		overflow: hidden;

		img {} }

	&__content {
		margin-left: 30px;
		flex: 1 1 auto;
		font-family: Montserrat;
		color: #706a88;
		font-size: 13px;
		font-weight: 500;
		line-height: 18.34px; }

	&__total {
		font-size: 16px;
		font-weight: 500;
		line-height: 22.58px;
		color: $blue-dark;
		margin: 15px -15px 0;
		display: flex;
		flex-wrap: wrap;

		& > * {
			margin: 0 15px;
			flex: 0 0 auto; }

		._old {
			color: #eb5757;
			position: relative;

			&::before {
				content: "";
				position: absolute;
				left: -10px;
				right: -10px;
				top: 50%;
				transform: translateY(-50%);
				height: 1px;
				background-color: #221849; } } }

	@include media-breakpoint-down(sm) {
		// display: block

		&__img {
			// float: left
			// margin: 0 30px 15px 0
			width: 100px;

 }			// height: 100px

		&__content {
			margin-left: 15px; } }


	@include media-breakpoint-only(xs) {
		display: block;

		&__img {
			margin-bottom: 15px;
			width: 150px;
			height-auto: 150px; }

		&__content {
			margin-left: 0; } } }
