.restoration-intro {
  position: relative;
  z-index: 1;
  padding-top: 115px;
  padding-bottom: 150px;

  &::before {
    content: "";
    position: absolute;
    z-index: -1;
    width: 2137px;
    height: 2137px;
    background-color: #ecf5fc;
    border-radius: 50%;
    bottom: -25px;
    left: 50%;
    transform: translateX(-50%); }

  &__content {
    position: relative;
    padding-right: 60%; }

  .title-1 {
    line-height: 55px; }

  &__img {
    position: absolute;
    right: 40px;
    max-width: 50%;
    top: 40%;
    transform: translateY(-50%); }

  &__info {
    color: rgba(36, 26, 75, 0.8);
    font-size: 20px;
    line-height: 28px;
    font-weight: 500; }

  &__action {
    display: flex;
    align-items: center;
    margin-top: 35px; }

  @include media-breakpoint-down(lg) {
    padding-top: 110px;
    padding-bottom: 90px;
    &::before {
      bottom: 10px; }
    &__content {
      padding-right: 0; }
    &__text {}
    &__img {
      transform: none;
      position: static;
      max-width: 80%;
      margin: 30px auto; }
    &__action {
      text-align: center; } }
  @include media-breakpoint-down(md) {
    &__action {
      display: flex;
      flex-direction: column; } }

  @include media-breakpoint-down(sm) {
    padding-top: 40px;
    &__action {}
    &__info {
      font-size: 16px; } } }

.nav-tabs {
  justify-content: center;
  @include media-breakpoint-down(lg) {
    justify-content: start; } }

.photo-restoration {
  &__item {
    max-width: 333px;
    min-width: 333px;
    background: #FFFFFF;
    border-radius: 15px;
    padding: 40px;
    margin: 0 10px;
    &:nth-child(3n-1) {
      box-shadow: 0px 25px 25px rgba(36, 26, 75, 0.03); } }
  .title-2 {
    color: #0085FF; }

  @include media-breakpoint-down(xl) {
    &__item {
      box-shadow: 0px 25px 25px rgba(36, 26, 75, 0.03); } }

  @include media-breakpoint-down(lg) {
    &__item {
      max-width: 325px;
      min-width: 325px; } }

  @include media-breakpoint-down(sm) {
    &__item {
      max-width: 90%;
      min-width: 90%;
      padding: 20px; } } }

.production-time {
  &__card {
    background: rgba(219, 218, 221, 0.3);
    border-radius: 30px; }
  p {
    max-width: 615px; } }

.making-order {
  &__item {
    max-width: 230px;
    margin: 0 20px 30px; }

  &__icon {
    width: 105px;
    height: 110px; }

  &__text {
    font-weight: 500;
    font-size: 16px;
    line-height: 23px;
    color: rgba(36, 26, 75, 0.8); }

  .btn {
    margin-top: 30px; }

  @include media-breakpoint-down(md) {
    &__text {
      text-align: center; } } }
