.card-small {
	display: flex;
	align-items: center;
	padding: 20px 30px;
	border-radius: 20px;
	border: 1px solid rgba(36, 26, 75, 0.1);
	background-color: #ffffff;

	&--shadow {
		box-shadow: 0 25px 40px rgba(134, 103, 255, 0.1); }

	._icon {
		flex: 0 0 auto;
		margin-right: 20px; }

	._title {
		color: #63618f; } }

.card-product {
	display: block;
	margin-bottom: 45px;
	position: relative;

	@include hover {
		.card-product__remove {
			opacity: 1; } }

	&__remove {
		transition: $tr;
		position: absolute;
		z-index: 10;
		right: 0;
		top: 0;
		width: 30px;
		height: 30px;
		background-color: #eb5757;
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 50%;
		cursor: pointer;
		@media (hover: hover) and (pointer: fine) {
			opacity: 0; }

		@include hover {
			transform: scale(1.5); }

		svg {
			width: 12px;
			height: 12px;
			fill: #fafafa; } }

	&__img {
		width: 100%;
		border-radius: 15px;
		background-color: #f2f2f2;
		position: relative;
		--bs-aspect-ratio: calc(230 / 270 * 100%);

		&:before {
			width: 100%;
			display: block;
			padding-top: var(--bs-aspect-ratio);
			content: ""; }

		div {
			display: flex;
			align-items: center;
			justify-content: center;
			text-align: center;
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%; } }

	&__title {
		display: block;
		transition: $tr;
		color: rgba(36, 26, 75, 0.6);
		font-size: 20px;
		margin-top: 20px;

		@include max(767) {
			font-size: 13px;
			margin-top: 10px; }

		@include hover {
			color: $blue-dark; }

		&::before {
			content: "";
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			z-index: 5; } }

	&__price {
		margin-top: 10px;
		color: rgba(36, 26, 75, 0.8);
		font-size: 16px;
		font-weight: 700;

		@include max(767) {
			font-size: 13px;
			margin-top: 5px; }
		._old {
			color: rgba(0, 0, 0, 0.8);
			font-size: 14px;
			font-weight: 300;
			line-height: normal;
			letter-spacing: normal;
			text-decoration: line-through; } } }

.card-tarif {
	padding: 35px;
	border-radius: 20px;
	border: 1px solid rgba(19, 17, 29, 0.1);
	background-color: #ffffff;
	text-align: center;
	width: 490px;
	max-width: 100%;
	display: block;

	@include hover {
		box-shadow: 0 25px 40px rgba(134, 103, 255, 0.1); }

	&__img {
		height: 125px;
		display: flex;
		align-items: center;
		justify-content: center; }

	&__period {
		color: #09005d;
		font-size: 28px;
		line-height: 37.8px;
		font-weight: 600;
		margin-top: 25px; }

	&__price {
		margin-top: 25px;
		color: $blue;
		font-size: 45px;
		line-height: normal;
		font-weight: 700; } }

.card-line {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	padding: 10px 30px;
	border-radius: 20px;
	border: 1px solid rgba(36, 26, 75, 0.1);
	background-color: #ffffff;
	margin: 10px 0;

	&:nth-child(2n-1) {
		background-color: #f2f2f2; }

	@include max(1399) {
		padding: 10px; }

	&__row {
		flex: 1 1 auto;
		display: flex;
		padding: 10px 0;
		flex-wrap: wrap;

		@include min(1400) {
			justify-content: space-between; } }

	&__col {
		flex: 1 0 auto;
		@include media-breakpoint-down(sm) {
			width: 100%; } }

	&__col-auto {
		flex: 0 0 auto; }

	&__text-sm {
		font-size: 12px;
		line-height: 16.7px;
		color: #706a88; }

	&__col,
	&__col-auto {
		margin: 10px;
		font-weight: 500;
		font-size: 16px;
		font-weight: 500;
		line-height: 22.58px;
		color: $blue-dark;

		strong {
			font-weight: 600; }

		.btn {
			box-shadow: none !important; } } }

.card-form {
	border: 1px solid rgba(36, 26, 75, 0.3);
	position: relative;
	width: 540px;
	max-width: 100%;

	&__remove {
		width: 17px;
		height: 17px;
		line-height: 0;
		position: absolute;
		top: 20px;
		right: 20px;
		fill: #8887a6;
		cursor: pointer;

		@include hover {
			fill: $blue;
			transform: scale(1.5); } }

	&--w-num {
		max-width: calc(100% - 40px);
		margin-left: 40px;

		@include media-breakpoint-down(sm) {
			margin-left: 0;
			max-width: 100%; } }

	&__num {
		position: absolute;
		color: $blue-dark;
		font-size: 20px;
		font-weight: 600;
		line-height: 30.18px;
		left: -40px;
		top: 0;
		@include media-breakpoint-down(sm) {
			left: 15px;
			top: 23px;

			& + .card-form__title {
				padding-left: 40px; } } }

	&__title {
		font-family: Montserrat;
		color: rgba(36, 26, 75, 0.8);
		font-size: 16px;
		font-weight: 500;
		line-height: 22.58px;
		padding: 13px 0;
		border: none;
		outline: none;
		border-bottom: 1px solid #cdc8e3;

		&:focus {
			border-color: $blue; }

		@include hover {
			border-color: $blue; } }

	&__description {
		font-family: Montserrat;
		color: rgba(36, 26, 75, 0.8);
		margin-top: 15px;
		margin-bottom: 15px;
		font-size: 12px;
		font-weight: 500;
		line-height: 16.93px;
		padding: 10px 0;
		border: none;
		outline: none;
		border-bottom: 1px solid #cdc8e3;

		&:focus {
			border-color: $blue; }

		@include hover {
			border-color: $blue; } }

	.form-control {
		border: 1px solid rgba(36, 26, 75, 0.1); } }

.card-add-new {
	border: 1px dashed rgba(0, 0, 0, 0.2);
	border-radius: 20px;
	display: flex;
	align-items: center;
	flex-direction: column;
	justify-content: center;
	height: calc(100% - 45px);
	text-align: center;
	padding: 15px;
	text-decoration: none;
	background-color: #fff;
	color: rgba(#000, 0.8);
	transition: $tr;

	@include hover {
		color: rgba(#000, 0.8);

		.card-add-new__img {
			transform: scale(1.3); } }

	&__img {
		transition: $tr; }

	&__title {
		margin-top: 20px; }

	@include media-breakpoint-down(sm) {
		font-size: 10px;
		line-height: 15px; } }

.card {
	.card-body {
		padding: 0; } }

.card-form {
	max-width: 333px;
	background: #FFFFFF;
	border: 1px solid rgba(36, 26, 75, 0.1);
	box-shadow: 0px 25px 25px rgba(36, 26, 75, 0.03);
	border-radius: 15px;
	padding: 30px 10px 20px;
	margin: 0 10px 20px;

	&__img {
		align-self: center; }

	&-new {
		border: 2px dashed rgba(0,0,0,0.2);
		max-width: 286px;
		min-width: 286px;
		min-height: 315px;
		height: auto;
		padding: 0 10px;
		margin: 0 10px 20px; }

	@include media-breakpoint-down(xl) {
		&-new {
			max-width: 333px;
			min-width: 333px; } }

	@include media-breakpoint-down(sm) {
		max-width: 100%;
		min-width: 100%;
		&-new {
			max-width: 100%;
			min-width: 100%; } } }
