.subnav {
  position: relative;
  margin-top: 25px;
  min-height: 67px;
  background: #fff;
  z-index: 2;
  align-items: center;
  border: 1px solid rgba(36, 26, 75, 0.1);
  box-shadow: 0px 8px 20px rgba(61, 58, 115, 0.03);
  &__link {
    font-size: 15px;
    line-height: 18px;
    color: #241A4B;
    cursor: pointer;
    &:last-child {
      &::after {
        display: none; } }
    &::after {
      content: '';
      padding-bottom: 5px;
      font-size: 20px;
      border: 1px solid rgba(36, 26, 75, 0.1);
      transform: rotate(90deg);
      margin: 0 45px; } }

  @include media-breakpoint-down(xxl) {
    &__link {
      &::after {
        margin: 0 35px; } } }

  @include media-breakpoint-down(xl) {
    &__link {
      &::after {
        margin: 0 15px; } } }

  @include media-breakpoint-down(lg) {
    padding-bottom: 10px;
    &__link {
      display: inline-block;
      margin-top: 10px; } }

  @include max(380) {
    &__link {
      margin-right: 30px;
      &::after {
        display: none; } } } }

.intro {
  position: relative;
  z-index: 1;
  padding-top: 210px;
  padding-bottom: 110px;

  &::before {
    content: "";
    position: absolute;
    z-index: -1;
    width: 2137px;
    height: 2137px;
    background-color: #ecf5fc;
    border-radius: 50%;
    bottom: -25px;
    left: 50%;
    transform: translateX(-50%); }

  &__content {
    position: relative;
    padding-right: 55%; }

  &__img {
    position: absolute;
    right: 40px;
    max-width: 50%;
    top: 40%;
    transform: translateY(-50%); }

  &__info {
    color: rgba(36, 26, 75, 0.8);
    font-size: 20px;
    line-height: 28px;
    font-weight: 500; }

  &__action {
    display: flex;
    align-items: center;
    margin-top: 20px; }

  @include media-breakpoint-down(lg) {
    padding-top: 110px;
    padding-bottom: 90px;
    &::before {
      bottom: 10px; }
    &__content {
      padding-right: 0; }
    &__text {}
    &__img {
      transform: none;
      position: static;
      max-width: 80%;
      margin: 30px auto; }
    &__action {
      text-align: center; } }
  @include media-breakpoint-down(md) {
    &__action {
      display: flex;
      flex-direction: column; } }

  @include media-breakpoint-down(sm) {
    padding-top: 40px;
    &__action {}
    &__info {
      font-size: 16px; } } }

.callback {
  padding-top: 160px;
  padding-bottom: 75px;
  text-align: center;
  color: #241A4B;

  &__title {
    font-weight: 600;
    font-size: 28px;
    line-height: 38px; }

  &__text,
  &__notice {
    font-weight: 500;
    font-size: 20px;
    line-height: 28px; }

  &__text {
    color: rgba(36, 26, 75, 0.6);
    margin-bottom: 10px; }

  &__tel {
    font-weight: 600;
    font-size: 20px;
    line-height: 24px; }

  &__input {
    width: 300px;
    padding-bottom: 15px;
    font-weight: 500;
    font-size: 16px;
    line-height: 23px;
    outline: none;
    border: none;
    border-bottom: 0.5px solid rgba(36, 26, 75, 0.5); }

  @include media-breakpoint-down(lg) {
    padding-top: 80px;
    padding-bottom: 60px; }

  @include media-breakpoint-down(md) {
    &__form {
      display: flex;
      flex-direction: column; }
    &__input {
      width: 100%;
      margin-bottom: 10px; } }

  @include media-breakpoint-down(sm) {
    &__notice,
    &__text,
    &__tel {
      font-size: 18px; } } }

.plan {
  margin-top: 100px;

  &__item {
    margin: 0 auto;
    padding: 60px 0 65px;
    background: #ECF5FC;
    border-radius: 30px;
    max-width: 1035px;
    text-align: center;
    color: #241A4B; }

  &__subtitle {
    font-weight: 600;
    font-size: 20px;
    line-height: 30px;
    margin-bottom: 10px; }

  &__title {
    font-size: 28px;
    line-height: 38px;
    font-weight: 600;
    margin-bottom: 90px; }

  &__card {
    margin-bottom: 95px;
    &:last-child {
      margin-bottom: 0; } }

  &__text {
    max-width: 435px;
    margin: 0 auto;
    font-weight: 500;
    font-size: 20px;
    line-height: 28px; }

  &__number {
    max-width: 530px;
    margin: 0 auto;
    font-weight: 600;
    font-size: 20px;
    line-height: 30px; }

  &__tel {
    color: #241A4B; }

  @include media-breakpoint-down(lg) {
    margin-top: 60px;
    &__item {
      padding: 60px 0; }
    &__title {
      margin-bottom: 60px; }
    &__card {
      margin-bottom: 60px; } }

  @include media-breakpoint-down(sm) {
    &__item {
      padding: 60px 10px; }
    &__subtitle {
      font-size: 18px; }
    &__title {
      margin-bottom: 50px; }
    &__text,
    &__number {
      font-size: 16px; } } }

.question {
  padding: 135px 0;
  text-align: center;

  &__text {
    font-weight: 500;
    font-size: 20px;
    line-height: 28px;
    color: rgba(36, 26, 75, 0.7);
    max-width: 750px;
    margin: 0 auto 110px; }

  &__title {
    font-weight: 600;
    font-size: 28px;
    line-height: 38px;
    color: #241A4B; }

  &__action {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 20px; }

  @include media-breakpoint-down(md) {
    padding-top: 110px;
    &__text {
      margin-bottom: 90px; }
    &__title {
      font-size: 24px; }
    &__action {
      flex-direction: column; } }

  @include media-breakpoint-down(sm) {
    padding-top: 90px;
    &__text {
      font-size: 16px;
      margin-bottom: 60px; }
    &__title {
      font-size: 18px; } } }

.banner-wide {
  &::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 80%;
    background: linear-gradient(90deg, #ECF5FC 37.4%, #CAE6FF 77.22%);
    bottom: 0; }
  &__img {
    width: 25%; }
  &__content {
    width: 70%; }
  &__text,
  &__tel {
    font-weight: 500;
    font-size: 20px;
    line-height: 28px; }
  &__text {
    color: rgba(36, 26, 75, 0.7); }

  @include max(1400) {
    &__content {
      margin-top: 80px; } }

  @include media-breakpoint-down(xl) {
    &__img {
      width: 30%; } }

  @include media-breakpoint-down(md) {
    .title-2 {
      font-size: 28px; } }

  @include media-breakpoint-down(sm) {
    &::before {
      height: 90%; }
    &__img {
      width: 40%; }
    &__content {
      padding-bottom: 30px;
      width: 90%; }
    &__text {
      font-size: 16px; } }

  @include max(400) {
    .title-2 {
      font-size: 26px; } } }

.conditions {
  text-align: center;
  .title-1 {
    color: #2196F3; }
  &__text,
  &__offer,
  .title-3 {
    max-width: 437px; }
  &__text {
    font-size: 16px;
    line-height: 23px;
    color: rgba(36, 26, 75, 0.8); } }

.services {
  position: relative;
  margin-top: 45px;
  margin-bottom: 80px;
  padding-top: 90px;
  &::before {
    content: '';
    position: absolute;
    background: #ECF5FC;
    left: 0;
    right: 0;
    top: 0;
    bottom: 155px; }
  &__content {
    position: relative;
    text-align: center; }
  &__subtitle {
    font-weight: 500;
    font-size: 20px;
    line-height: 28px;
    color: #241A4B;
    margin-bottom: 40px; }
  &__text {
    font-weight: 500;
    font-size: 20px;
    line-height: 28px;
    color: rgba(36, 26, 75, 0.6); }

  @include media-breakpoint-down(md) {
    padding-top: 60px;
    margin-bottom: 50px;
    .title-2 {
      font-size: 24px; }
    &__subtitle,
    &__text {
      br {
        display: none; } } }

  @include media-breakpoint-down(sm) {
    padding-top: 40px;
    margin-bottom: 30px;
    &__subtitle {
      font-size: 18px;
      margin-bottom: 30px; }
    &__text {
      font-size: 16px; } } }

.service {
  &__cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 40px;
    &:last-child {
      margin-bottom: 0; } }
  &__card {
    padding: 30px 0 40px;
    max-width: 333px;
    min-width: 333px;
    background: #FFFFFF;
    border: 1px solid rgba(36, 26, 75, 0.1);
    box-shadow: 0px 25px 40px rgba(134, 103, 255, 0.1);
    border-radius: 20px;
    margin: 0 10px 20px; }
  &__name {
    font-weight: 600;
    font-size: 28px;
    line-height: 38px;
    color: #09005D;
    margin-bottom: 25px; }
  &__subject {
    font-weight: 500;
    font-size: 16px;
    line-height: 28px;
    color: rgba(36, 26, 75, 0.8);
    margin-bottom: 15px;
    &:last-child {
      margin-bottom: 30px; } }
  &__cost {
    font-weight: 600;
    font-size: 28px;
    line-height: 38px;
    color: rgba(36, 26, 75, 0.8);
    margin-bottom: 20px; }

  @include media-breakpoint-down(xl) {
    &__card {
      max-width: 328px;
      min-width: 328px; } }

  @include media-breakpoint-down(sm) {
    &__name {
      font-size: 24px; }
    &__cost {
      font-size: 24px; } }

  @include max(380) {
    &__card {
      min-width: 280px; }
    &__subject {
      line-height: 24px;
      margin-bottom: 10px; } } }

.slider {
  max-width: 900px;
  margin: 0 auto;
  &__arrow {
    position: absolute;
    z-index: 2;
    top: 105px;
    cursor: pointer;
    &-prev {
      left: -60px; }
    &-next {
      right: -60px;
      transform: rotate(180deg); } }

  @include media-breakpoint-down(xl) {
    max-width: 600px; }

  @include media-breakpoint-down(md) {
    max-width: 300px; }

  @include media-breakpoint-down(sm) {
    &__arrow {
      top: 106%;
      &-prev {
        left: 60px; }
      &-next {
        right: 60px; } } } }

.slides {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 0 20px;
  width: 260px;
  &:focus {
    outline: none; } }

.products {
  @include media-breakpoint-down(md) {
    .title-2 {
      font-size: 24px; } } }

.registration {
  position: relative;
  z-index: 1;
  &::before {
    content: '';
    position: absolute;
    z-index: -1;
    width: 2240px;
    height: 1370px;
    background-color: #ecf5fc;
    border-radius: 50%;
    bottom: -25px;
    left: 50%;
    top: 0;
    transform: translateX(-50%); }

  &__title {
    font-weight: 600;
    font-size: 28px;
    line-height: 38px;
    color: #241A4B;
    text-align: center; }

  &__cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 120px; }

  .card {
    max-width: 333px;
    min-width: 333px;
    min-height: 278px;
    padding: 35px 0 25px;
    text-align: center;
    box-shadow: 0px 25px 40px rgba(0, 133, 255, 0.1);
    border-radius: 15px;
    margin: 0 10px 20px; }

  &__text {
    font-weight: 500;
    font-size: 20px;
    line-height: 28px;
    color: rgba(36, 26, 75, 0.7);
    max-width: 240px;
    margin: 0 auto 30px; }

  &__btn {
    margin: 0 auto;
    width: 180px; }

  @include media-breakpoint-down(xl) {
    .card {
      max-width: 328px;
      min-width: 328px; } }

  @include media-breakpoint-down(lg) {
    &::before {
      height: 1570px; } }

  @include media-breakpoint-down(md) {
    br {
      display: none; }
    &::before {
      height: 2260px; } }

  @include media-breakpoint-down(sm) {
    &__cards {
      margin-bottom: 60px; }
    &__text {
      font-size: 18px;
      margin-bottom: 25px; } }

  @include max(440) {
    &::before {
      height: 2400px; } }

  @include max(380) {
    .card {
      min-width: 280px; } } }

.organization {
  &__title {
    font-weight: 600;
    font-size: 28px;
    line-height: 38px;
    color: #241A4B;
    text-align: center; }

  &__cards {
    display: flex;
    flex-wrap: wrap; }

  &__card {
    width: calc((100% - 4 * 45px) / 4);
    margin: 0 22.5px 20px; }

  &__text {
    font-weight: 500;
    font-size: 16px;
    line-height: 23px;
    color: rgba(36, 26, 75, 0.8); }

  @include media-breakpoint-down(xl) {
    &__card {
      width: calc((100% - 4 * 30px) / 4);
      margin: 0 14.5px 20px; } }

  @include media-breakpoint-down(lg) {
    &__card {
      width: calc((100% - 2 * 45px) / 2);
      margin: 0 22.5px 20px; } }

  @include media-breakpoint-down(md) {
    &__cards {
      text-align: center; }
    &__card {
      width: 100%;
      margin: 0 0 35px; } } }

.feedbacks {
  padding-bottom: 130px;
  position: relative;
  z-index: 1;
  text-align: center;

  &__subtitle {
    font-weight: 500;
    font-size: 20px;
    line-height: 28px;
    color: #241A4B; }

  .slider__arrow {
    top: 40%; }

  @include media-breakpoint-down(xl) {
    padding-bottom: 100px; }

  @include media-breakpoint-down(sm) {
    padding-bottom: 60px;
    .slider__arrow {
      top: 82%; } } }

.feedback-slide {
  text-align: left;
  background: #FFFFFF;
  box-shadow: 0px 25px 25px rgba(36, 26, 75, 0.03);
  border-radius: 15px;
  margin-bottom: 60px;

  &__title {
    font-weight: 600;
    font-size: 20px;
    line-height: 30px;
    color: #241A4B;
    margin-bottom: 5px; }

  &__date {
    font-weight: 500;
    font-size: 12px;
    line-height: 17px;
    color: rgba(36, 26, 75, 0.4); }

  &__text {
    font-weight: 500;
    font-size: 16px;
    line-height: 23px;
    color: rgba(36, 26, 75, 0.7); }

  @include media-breakpoint-down(sm) {
    margin-bottom: 120px; } }

.location {
  &__item {
    position: relative;
    height: 353px; } }

.map {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 45px;
  width: 100%;
  height: 306px;
  border-radius: 15px;
  overflow: hidden;
  &-google {
    background-image: url(../img/map.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover; }
  iframe {
    width: 100%;
    height: 100%;
    border: 0; } }
