.title-1 {
	font-family: Montserrat;
	color: $blue-dark;
	font-size: 40px;
	line-height: normal;
	font-weight: 600;
	letter-spacing: normal;

	@include media-breakpoint-down(md) {
		font-size: 28px;
		line-height: 38px; }
	@include media-breakpoint-down(md) {
		font-weight: 600; } }

.title-2 {
	font-family: Montserrat;
	color: #09005d;
	font-size: 28px;
	line-height: 38px;
	font-weight: 600;
	letter-spacing: normal;
	margin-top: 60px;
	margin-bottom: 45px;

	@include media-breakpoint-down(md) {
		font-size: 20px;
		line-height: 30px; }

	@include media-breakpoint-down(sm) {
		margin-top: 30px;
		margin-bottom: 15px; } }

.title-3 {
	font-family: Montserrat;
	color: #09005d;
	font-size: 20px;
	line-height: 30px;
	font-weight: 600;
	letter-spacing: normal;

	@include media-breakpoint-down(md) {
		font-size: 20px;
		line-height: 30px; } }

@each $name, $val in $colors {
	.text-#{$name} {
		color: $val; } }

@each $name, $val in $colors {
	.bg-#{$name} {
		background-color: $val; } }

.bg-gradient-2 {
	background-image: linear-gradient(to right, #ecf5fc 0%, #cae6ff 100%); }

.banner {
	position: relative;
	z-index: 1;
	padding: 30px 50px;
	border-radius: 15px;
	overflow: hidden;
	display: flex;
	justify-content: space-between;

	&__content {
		max-width: 44%;
		display: flex;
		flex-direction: column;
		justify-content: center; }

	&__img {
		flex: 0 0 auto;
		margin: (-30px) -51px -30px 30px;
		max-width: 54%;
		display: flex;
		align-items: center;
		justify-content: flex-end; }

	&__bg {
		position: absolute;
		left: -100vw;
		right: -1px;
		top: 0;
		bottom: 0;
		z-index: -1;

		img {
			height: 100%;
			width: 100%;
			object-fit: contain;
			object-position: 100% 50%; } }

	@include media-breakpoint-down(xl) {
		padding: 15px 30px;

		&__img {
			margin: (-15px) -31px -15px 30px; } }

	@include media-breakpoint-down(md) {
		padding: 15px;

		&__content {
			max-width: 100%;
			font-size: 10px; }

		&__img {
			max-width: 40%;
			margin: (-15px) -15px -15px 30px; } } }
