.gallery {
	display: flex;

	&--fullscreen {
		@include media-breakpoint-up(md) {
			position: fixed;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			z-index: 910;
			background-color: #fff;
			padding: 30px 45px;

			.gallery__slider,
			.slick-list,
			.slick-track,
			.slick-slide {
				height: calc( 100vh - 260px ); }

			.slick-slide {
				align-items: center;
				justify-content: center;
				display: flex; } } }

	&__slider {
		margin-left: 50px;
		width: calc(100% - 200px);
		width: 530px;
		height: 530px;
		border-radius: 20px;
		overflow: hidden;
		background: rgba(136, 135, 166, 0.1);



		@include media-breakpoint-down(sm) {
 }			// margin-bottom: 0


		.slick-list,.slick-track {
			height: 100%; }
		.slick-arrow {
			position: absolute;
			z-index: 2;
			top: calc(50% - 19px);
			fill: white;
			cursor: pointer;
			&::before {
				position: absolute;
				content: '';
				left: -15px;
				right: -15px;
				top: -15px;
				bottom: -15px; }

			svg {
				height: 38px;
				width: 23px; }


			&.slick-prev {
				left: 20px; }

			&.slick-next {
				right: 20px;
				transform: rotate(180deg); }

			&:hover {

				fill: $main; } } }

	&__link {
		text-align: right;
		margin-bottom: 12px;

		@include media-breakpoint-up(md) {
			margin-top: -22px; }


		@include media-breakpoint-down(sm) {
			order: -1; }


		svg {
			max-width: 20px;
			max-height: 20px;
			fill: $sec;
			margin-left: 4px; } }

	&__slide {
		height: 100%;
		position: relative;
		outline: none;

		&-center {
			width: 100%;
			height: 100%;
			text-align: center;
			display: flex;
			align-items: center;
			justify-content: center; }

		&[data-gallery] {
			cursor: pointer; }

		img {
			display: inline-block;
			vertical-align: middle; }

		&--video::before {
			content: "";
			position: absolute;
			z-index: 4;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			width: 80px;
			height: 80px;
			background-size: contain;
			background-repeat: no-repeat;
			background-position: 50% 50%;
			background-image: url(../img/play.svg);
			transition: 0.2s;

			@include media-breakpoint-down(sm) {
				width: 48px;
				height: 48px; } }

		&:active::before {
			opacity: 1;
			transform: translate(-50%, -50%) scale(0.5);
			transition: 0.03s; } }

	&__footer {
		position: relative;
		user-select: none;
		order: -1;
		flex: 0 0 auto;

		&-scroller {
			width: 145px;
			height: 475px;
			overflow: auto;

			&::-webkit-scrollbar {
				width: 0;
				background: transparent; } } }

	&__thumb {
		height: 145px;
		width: 145px;
		border-radius: 10px;
		overflow: hidden;
		cursor: pointer;
		position: relative;

		&::before {
			content: '';
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			border-radius: 10px;
			background-color: $main;
			opacity: 0;
			transition: $tr; }


		&:not(:last-child) {
			margin-bottom: 20px; }

		@include cover;

		&:hover {
			&::before {
				opacity: 0.5; } }
		&.active {
			pointer-events: none;
			img {
				opacity: 0.5; } } }

	&__prev,
	&__next {
		bottom: 0;
		position: absolute;
		height: 48px;
		width: 48px;
		fill: #CDC8E3;
		z-index: 3;
		display: flex;
		align-items: center;
		justify-content: center;
		cursor: pointer;
		transition: 0.15s;

		&._disabled {
			pointer-events: none;
			fill: #ccc;
			opacity: 0; }

		&:hover {
			fill: $blue; }

		svg {
			margin-right: 0px;
			width: 23px;
			height: 38px; } }

	&__prev {
		left: 5px;
		transform: rotate(90deg);

		@include media-breakpoint-down(sm) {
			// left: 5px
			display: none; } }

	&__next {
		right: 5px;

		@include media-breakpoint-down(sm) {
			// right: 5px
			display: none; }


		transform: rotate(-90deg); }



	@include media-breakpoint-down(lg) {

		width: 100vw;
		&__slider {
			overflow-x: auto;
			display: flex;
			height: auto;
			background: none;
			border-radius: 0;
			align-items: center;
			margin: 0 -15px;
			width: 100%;
			&:before,
			&:after {
				content: '';
				width: 15px;
				height: 100%;
				display: block;
				flex: 0 0 auto; } }
		&__slide {
			border-radius: 15px;
			overflow: hidden;
			flex: 0 0 auto;
			height: auto;
			width: auto;
			&:not(:last-child) {
				margin-right: 15px; }

			img {

				max-width: 80vw;
				max-height: 80vw; } }

		&__footer,
		&__thumb,
		&__prev,
		&__next {
			display: none; } }


	@include media-breakpoint-only(md) {
		margin-left: calc( (100vw - 720px) / -2);
		&__slider {
			&:before,
			&:after {
				width: calc( (100vw - 720px) / 2 + 15px); }

			img {
				max-width: 720px - 30px; } } }


	@include media-breakpoint-only(sm) {
		margin-left: calc( (100vw - 540px) / -2);
		&__slider {
			&:before,
			&:after {
				width: calc( (100vw - 540px) / 2 + 15px); }
			img {
				max-width: 540px - 30px; } } } }
