.first {
	position: relative;
	z-index: 1;
	padding-top: 150px;
	padding-bottom: 45px;

	&::before {
		content: "";
		position: absolute;
		z-index: -1;
		width: 2137px;
		height: 2137px;
		background-color: #ecf5fc;
		border-radius: 50%;
		bottom: 60px;
		left: 50%;
		transform: translateX(-50%); }

	&__content {
		position: relative;
		padding-right: 55%; }

	.title-1 {
		font-weight: 700; }

	&__text {
		margin-top: 30px;
		color: $blue-dark;
		font-size: 20px;
		line-height: 28.22px; }

	&__img {
		position: absolute;
		right: 0;
		max-width: 50%;
		top: 50%;
		transform: translateY(-50%); }

	&__action {
		margin-top: 50px;

		._info {
			color: rgba(36, 26, 75, 0.8);
			font-size: 16px;
			line-height: 22.58px;
			font-weight: 500;
			margin-bottom: 20px; } }

	&__footer {
		margin-top: 160px;
		display: flex;
		justify-content: center;
		flex-wrap: wrap;

		._col {
			flex: 0 0 auto;
			width: 340px;
			max-width: 100%;
			margin: 10px;

			.card-small {
				height: 100%; } } }

	@include media-breakpoint-down(lg) {
		&__content {
			padding-right: 0; }
		&__text {}
		&__img {
			transform: none;
			position: static;
			max-width: 80%;
			margin: 30px auto; }
		&__action {
			text-align: center; }
		&__footer {
			margin-top: 60px; } }
	@include media-breakpoint-down(sm) {
		padding-top: 40px;
		&__action {}
		._info {
			font-size: 13px; } } }

.partner-step {
	&__img {
		width: 105px;
		height: 105px;
		display: flex;
		align-items: center;
		justify-content: center;
		border: 1px solid #64b3fd;
		border-radius: 50%;
		position: relative;

		._num {
			position: absolute;
			top: -5px;
			left: 0;
			border-radius: 50%;
			color: #ffffff;
			font-size: 20px;
			line-height: 30.18px;
			font-weight: 600;
			display: flex;
			align-items: center;
			text-align: center;
			justify-content: center;
			padding: 0 10px;
			min-width: 37px;
			height: 37px;
			box-shadow: 0 15px 20px rgba(33, 150, 243, 0.2);
			background-color: $blue; } }

	&__title {
		color: #09005d;
		font-size: 20px;
		line-height: 30.18px;
		font-weight: 600;
		margin-top: 30px; }

	&__text {
		color: rgba(36, 26, 75, 0.8);
		font-size: 16px;
		line-height: 22.58px;
		font-weight: 500;
		margin-top: 20px; }

	@include media-breakpoint-down(sm) {
		&__img {}
		&__title {
			margin-top: 20px; }
		&__text {} } }
