.product-banner {
	padding: 45px 60px;
	padding-right: calc(50% - 120px);
	color: #ffffff;
	position: relative;
	background-color: $blue-dark;
	margin: 80px 0;
	border-radius: 15px;



	&__img {
		width: calc(50% - 120px);
		position: absolute;
		text-align: center;
		right: 60px;
		bottom: 20px; }

	&__title {
		font-size: 28px;
		line-height: 37.8px;
		font-weight: 600; }

	&__text {
		font-size: 20px;
		line-height: 28.22px;
		font-weight: 500;
		opacity: 0.9;
		margin-top: 20px; }

	&__price {
		margin-top: 27px;
		font-size: 40px;
		line-height: 54.56px;
		font-weight: 600; }

	&__info {
		margin-top: 20px;
		font-size: 16px;
		line-height: 22.58px;
		font-weight: 500; }

	&__action {
		margin-top: 10px; }

	@include media-breakpoint-down(sm) {
		margin: 40px 0;
		padding: 20px;
		&__img {
			display: none; }
		&__title {
			font-weight: 500;
			font-size: 16px;
			line-height: 1.4; }
		&__text {
			margin-top: 5px;
			font-size: 10px;
			line-height: 15px; }
		&__price {
			font-size: 20px;
			line-height: 1.4;
			margin-top: 15px; }
		&__info {
			margin-top: 15px;
			font-size: 10px;
			line-height: 15px; }
		&__action {} } }
