.chosen-container {
	position: relative;
	display: inline-block;
	vertical-align: middle;
	font-size: 13px;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	width: 100% !important;
	border-radius: 5px;
	background-color: #fff;

	* {
		box-sizing: border-box; }

	.chosen-drop {
		position: absolute;
		top: calc(100% - 1px);
		z-index: 1010;
		width: 100%;
		border-top: 0;
		background: #fff;
		clip: rect(0, 0, 0, 0);
		clip-path: inset(100% 100%);
		border: 1px solid $primary;
		padding: 0; }

	&.chosen-with-drop {
		.chosen-single {
			border-radius: 5px; }

		.chosen-drop {
			clip: auto;
			clip-path: none; } }

	a {
		cursor: pointer; }

	.search-choice .group-name, .chosen-single .group-name {
		margin-right: 4px;
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
		font-weight: normal;
		color: #999999; }

	.search-choice .group-name:after, .chosen-single .group-name:after {
		content: ":";
		padding-left: 2px;
		vertical-align: top; } }

.chosen-container-single {
	.chosen-single {
		position: relative;
		display: block;
		overflow: hidden;
		background-clip: padding-box;
		text-decoration: none;
		white-space: nowrap;
		border-radius: 5px;
		border: 1px solid #ced4da;
		height: 50px;

		&:hover {
			border-color: $primary; }

		&:active,
		&:focus {
			border-color: $primary; } }

	.chosen-default {
		color: #999; }

	.chosen-single span {
		display: block;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;

		color: $blue-dark;
		font-size: 16px;
		font-weight: 500;
		line-height: 48px;
		padding-left: 22px;
		padding-right: 50px; }

	.chosen-single-with-deselect span {
		margin-right: 38px; }

	.chosen-single abbr {
		position: absolute;
		top: 6px;
		right: 26px;
		display: block;
		width: 12px;
		height: 12px;
		background: url("../img/chosen-sprite.png") -42px 1px no-repeat;
		font-size: 1px;

		&:hover {
			background-position: (-42px) -10px; } }

	&.chosen-disabled .chosen-single abbr:hover {
		background-position: (-42px) -10px; }

	.chosen-single div {
		position: absolute;
		top: 0;
		bottom: 0;
		right: 0;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 50px;
		height: 100%;

		b {
			display: block;
			width: 15px;
			height: 9px;
			background: url("../img/chosen-arrows.svg") no-repeat center; } }

	.chosen-search {
		position: relative;
		z-index: 1010;
		margin: 0;
		padding: 3px 4px;
		white-space: nowrap;

		input[type="text"] {
			margin: 10px 17px 0;
			margin-bottom: 0;
			padding: 4px 20px 4px 5px;
			border-radius: 5px;
			width: calc(100% - 34px);
			height: auto;
			outline: 0;
			border: 1px solid rgba(36, 26, 75, 0.3);
			background: url("../img/chosen-sprite.png") no-repeat 100% -20px;
			font-size: 1em;
			font-family: sans-serif;
			line-height: normal; } }

	.chosen-drop {
		background-clip: padding-box; }

	&.chosen-container-single-nosearch .chosen-search {
		position: absolute;
		clip: rect(0, 0, 0, 0);
		clip-path: inset(100% 100%); } }

.chosen-container .chosen-results {
	color: #444;
	position: relative;
	overflow-x: hidden;
	overflow-y: auto;
	max-height: 240px;
	margin: 0;
	color: #505862;
	// font-family: "SFUI Display"
	font-size: 15px;
	font-weight: 400;
	line-height: 30px;
	padding: 10px 0;

	li {
		display: none;
		margin: 0;
		padding: 8px 15px;
		list-style: none;
		line-height: 15px;
		word-wrap: break-word;

		&.active-result {
			display: list-item;
			cursor: pointer; }

		&.disabled-result {
			display: list-item;
			color: #ccc;
			cursor: default; }

		&.highlighted {
			background-color: #e4ebf2; }

		&.no-results {
			color: #777;
			display: list-item;
			background: #f4f4f4; }

		&.group-result {
			display: list-item;
			font-weight: bold;
			cursor: default; }

		&.group-option {
			padding-left: 15px; }

		em {
			font-style: normal;
			text-decoration: underline; } } }

.chosen-container-multi {
	.chosen-choices {
		position: relative;
		overflow: hidden;
		margin: 0;
		width: 100%;
		height: auto;
		cursor: text;
		border: 1px solid #ced4da;
		height: 45px;
		color: #798795;
		// font-family: "SFUI Display"
		font-size: 15px;
		font-weight: 500;
		line-height: 14px;
		padding-left: 20px;

		li {
			float: left;
			list-style: none;

			&.search-field {
				margin: 0;
				padding: 0;
				white-space: nowrap;

				input[type="text"] {
					margin: 1px 0;
					padding: 0;
					height: 25px;
					outline: 0;
					border: 0 !important;
					background: transparent !important;
					color: #999;
					font-size: 100%;
					font-family: sans-serif;
					line-height: normal;
					border-radius: 0;
					width: 25px; } }

			&.search-choice {
				position: relative;
				margin: 3px 5px 3px 0;
				padding: 3px 20px 3px 5px;
				border: 1px solid #aaa;
				max-width: 100%;
				border-radius: 3px;
				background-color: #eeeeee;
				background-image: linear-gradient(#f4f4f4 20%, #f0f0f0 50%, #e8e8e8 52%, #eee 100%);
				background-size: 100% 19px;
				background-repeat: repeat-x;
				background-clip: padding-box;
				color: #333;
				line-height: 13px;
				cursor: default;

				span {
					word-wrap: break-word; }

				.search-choice-close {
					position: absolute;
					top: 4px;
					right: 3px;
					display: block;
					width: 12px;
					height: 12px;
					background: url("../img/chosen-sprite.png") -42px 1px no-repeat;
					font-size: 1px;

					&:hover {
						background-position: (-42px) -10px; } } }

			&.search-choice-disabled {
				padding-right: 5px;
				border: 1px solid #ccc;
				background-color: #e4e4e4;
				color: #666; }

			&.search-choice-focus {
				background: #d4d4d4;

				.search-choice-close {
					background-position: (-42px) -10px; } } } }

	.chosen-results {
		margin: 0;
		padding: 0; }

	.chosen-drop .result-selected {
		display: list-item;

		// color: #ccc
		cursor: default; } }

.chosen-container-active {
	.chosen-single {
		border-color: $primary; }

	&.chosen-with-drop .chosen-single {
		div {
			border-left: none;
			background: transparent; } }

	.chosen-choices {
		border: 1px solid #5897fb;

		li.search-field input[type="text"] {
			color: #222 !important; } } }

.chosen-disabled {
	opacity: 0.5 !important;
	cursor: default;

	.chosen-single, .chosen-choices .search-choice .search-choice-close {
		cursor: default; } }

.chosen-rtl {
	text-align: right;

	.chosen-single {
		overflow: visible;
		padding: 0 8px 0 0;

		span {
			margin-right: 0;
			margin-left: 26px;
			direction: rtl; } }

	.chosen-single-with-deselect span {
		margin-left: 38px; }

	.chosen-single {
		div {
			right: auto;
			left: 3px; }

		abbr {
			right: auto;
			left: 26px; } }

	.chosen-choices li {
		float: right;

		&.search-field input[type="text"] {
			direction: rtl; }

		&.search-choice {
			margin: 3px 5px 3px 0;
			padding: 3px 5px 3px 19px;

			.search-choice-close {
				right: auto;
				left: 4px; } } }

	&.chosen-container-single .chosen-results {
		margin: 0 0 4px 4px;
		padding: 0 4px 0 0; }

	.chosen-results li.group-option {
		padding-right: 15px;
		padding-left: 0; }

	&.chosen-container-active.chosen-with-drop .chosen-single div {
		border: none; }

	.chosen-search input[type="text"] {
		padding: 4px 5px 4px 20px;
		background: url("../img/chosen-sprite.png") no-repeat -30px -20px;
		direction: rtl; }

	&.chosen-container-single {
		.chosen-single div b {
			background-position: 6px 2px; }

		&.chosen-with-drop .chosen-single div b {
			background-position: (-12px) 2px; } }

	.chosen-rtl .chosen-search input[type="text"] {
		background-image: url("../img/chosen-sprite@2x.png") !important;
		background-size: 52px 37px !important;
		background-repeat: no-repeat !important; }

	.chosen-container-single {
		.chosen-single {
			abbr, div b {
				background-image: url("../img/chosen-arrow@2x.png") !important;
				background-size: 13px 10px !important;
				background-repeat: no-repeat !important; } }

		.chosen-search input[type="text"] {
			background-image: url("../img/chosen-sprite@2x.png") !important;
			background-size: 52px 37px !important;
			background-repeat: no-repeat !important; } }

	.chosen-container-multi .chosen-choices .search-choice .search-choice-close {
		background-image: url("../img/chosen-sprite@2x.png") !important;
		background-size: 52px 37px !important;
		background-repeat: no-repeat !important; }

	.chosen-container {
		.chosen-results-scroll-down span, .chosen-results-scroll-up span {
			background-image: url("../img/chosen-sprite@2x.png") !important;
			background-size: 52px 37px !important;
			background-repeat: no-repeat !important; } } }

