.chosen-title {
	display: flex;
	text-align: center;
	justify-content: space-between;
	color: #000;
	font-size: 14px;
	line-height: 1.4;
	border-bottom: 1px solid #eee;

	.title-item {
		padding: 4px;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 12px;
		font-weight: normal;
		flex: 1 1 1px;

		// word-break: break-all

		&+.title-item {
			border-left: 1px solid #eee; } } }

.baron-column._scrollbar .chosen-results.baron__scroller {
	padding-right: 0; }


[data-is_sizechoose] + .chosen-container .chosen-results li {
	display: flex;
	justify-content: space-between;
	padding-left: 0;
	padding-right: 0;
	.size-value {
		flex: 1 1 auto;
		text-align: center; } }
