.header {
	position: fixed;
	z-index: 777;
	left: 0;
	right: 0;
	top: 0;
	border: 1px solid #e0e0e0;
	background-color: #fff;

	img {
		vertical-align: middle; }

	.container {
		height: 85px;
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 100%;
		@include media-breakpoint-down(md) {
			height: 59px; }

		& > * {
			flex: 0 0 auto; } }

	&__logo {
		display: flex;
		align-items: center;
		color: rgba(0, 0, 0, 0.3);

		@include link;

		._img {
			margin-right: 10px;
			height: 40px;
			width: 40px; } }

	&__city {
		display: flex;
		align-items: center;
		color: #241a4b;
		font-weight: 600;

		@include link;

		& > * {
			flex: 0 0 auto; }

		._img {
			margin-right: 14px;
			width: 19px;
			height: 27px; } }

	&__nav {
		flex: 0 1 auto;

		&-item {
			color: #706a88;
			font-size: 15px;
			margin: 10px 0;
			margin-left: 25px;

			@include link;

			&.active {
				color: $blue-dark; } } }

	&__actions {
		display: flex;
		align-items: center;

		& > * {
			flex: 0 0 auto; } }

	&__cart {
		position: relative;
		width: 26px;
		height: 23px;
		margin-left: 25px;

		._quant {
			position: absolute;
			z-index: 3;
			width: 10px;
			height: 10px;
			background-color: #ff0000;
			border-radius: 50%;
			right: -2px;
			top: 0; } }

	&__user {
		color: #221849;
		@include link;

		display: flex;
		align-items: center;
		margin-left: 25px;
		color: #221849;
		font-size: 15px;

		& > * {
			flex: 0 0 auto; }

		._img {
			margin-left: 10px; }

		@include media-breakpoint-down(md) {
			display: none; } }
	&__btn-menu {
		margin-left: 15px;
		width: 36px;
		height: 36px;
		border-radius: 50%;
		transition: $tr;
		position: relative;
		cursor: pointer;
		border: none;
		background: none;
		text-decoration: none;

		img {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%); }

		@include hover {
			background-color: rgba(33, 150, 243, 0.1); }

		&:active {
			transition: 0s;
			background-color: rgba(33, 150, 243, 0.5); }

		@include media-breakpoint-up(lg) {
			display: none; } }

	@include media-breakpoint-down(xl) {

		&__logo {
			// display: none
			margin-right: 15px;
			font-size: 14px; }

		&__city {
			margin-right: 30px;
			font-size: 14px;
			._img {
				display: none; } }
		&__nav {
			display: flex;
			flex-wrap: wrap;
			flex: 0 1 auto !important;
			&-item {
				font-size: 14px;
				margin: 3px 8px; } } }

	@include media-breakpoint-down(lg) {
		&__city,
		&__nav {

			display: none; } } }

.header-account {
	position: fixed;
	left: 290px;
	right: 0;
	top: 0;
	z-index: 777;
	background-color: #F9F9FB;
	border-bottom: 1px solid #E3E8E4;
	user-select: none;

	.container {
		height: 80px;
		display: flex;
		align-items: center;
		justify-content: space-between;

		& > div {
			display: flex;
			align-items: center; } }

	&__btn-sidebar {
		margin-right: 15px;
		width: 36px;
		height: 36px;
		border-radius: 50%;
		transition: $tr;
		position: relative;
		cursor: pointer;
		border: none;
		background: none;
		text-decoration: none;

		img {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%); }

		@include hover {
			background-color: rgba(33, 150, 243, 0.1); }

		&:active {
			transition: 0s;
			background-color: rgba(33, 150, 243, 0.5); }

		@include media-breakpoint-up(lg) {
			display: none; } }

	&__btn-menu {
		margin-left: 15px;
		width: 36px;
		height: 36px;
		border-radius: 50%;
		transition: $tr;
		position: relative;
		cursor: pointer;
		border: none;
		background: none;
		text-decoration: none;

		img {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%); }

		@include hover {
			background-color: rgba(33, 150, 243, 0.1); }

		&:active {
			transition: 0s;
			background-color: rgba(33, 150, 243, 0.5); } }

	&__avatar {
		margin-left: 30px; }

	&__name {
		margin-left: 30px;
		color: #221849;
		font-size: 15px;
		font-weight: 500;
		line-height: normal;
		letter-spacing: normal; }

	&__menu {
		position: absolute;
		background-color: #fff;
		border-radius: 15px;
		border: 1px solid #E0E0E0;
		top: calc(100% + 20px);
		padding: 0px 20px;
		right: 0;
		box-shadow: 0px 6px 6px rgba(0, 0, 0, 0.05);
		font-size: 15px;
		line-height: 18px;
		opacity: 0;
		pointer-events: none;
		transition: $tr;

		&._show {
			opacity: 1;
			pointer-events: auto; }

		ul {
			list-style-type: none;
			padding: 0;
			margin: 0;

			li {
				margin: 20px 0; } }

		&-link {
			background: none;
			text-decoration: none;
			border: none;
			color: $blue-dark;
			padding: 0;

			@include hover {
				color: $blue; } } }

	&__notification {
		position: absolute;
		background-color: #fff;
		border-radius: 15px;
		border: 1px solid #E0E0E0;
		top: calc(100% + 20px);
		width: 300px;
		padding: 20px;
		left: 30px;
		transform: translateX(-50%);
		box-shadow: 0px 6px 6px rgba(0, 0, 0, 0.05);
		font-size: 15px;
		line-height: 18px;
		opacity: 0;
		pointer-events: none;
		transition: $tr;

		@include media-breakpoint-down(sm) {
			left: auto;
			right: 0;
			transform: none;
			max-width: calc(100vw - 24px); }

		&._show {
			opacity: 1;
			pointer-events: auto; }

		&::before {
			top: -40px;
			left: 0;
			right: 0;
			bottom: 100%;
			content: "";
			position: absolute; } }

	&__btn-notification {
		margin-left: 15px;
		border: none;
		background: none;
		text-decoration: none;
		position: relative;

		&._unread:before {
			position: absolute;
			content: "";
			right: -1px;
			border-radius: 50%;
			top: 2px;
			width: 12px;
			height: 12px;
			border: 2px solid #ffffff;
			background-color: #ff0000; } }

	@include media-breakpoint-down(lg) {
		left: 0; }

	@include media-breakpoint-up(md) {
		.container {
			max-width: calc(100% - 30px); } } }

.mobile-menu {
	padding: 30px 0;
	position: fixed;
	z-index: -100;
	/////!!!!!!!!!!!!!!!!!!!!!!!!!
	opacity: 0;
	left: 0;
	top: 60px;
	bottom: 0;
	right: 0;
	background-color: #fff;
	@include media-breakpoint-up(md) {
		top: 80px; }
	@include media-breakpoint-up(lg) {
		display: none; }

	&__city {
		display: flex;
		align-items: center;

		@include link;

		& > * {
			flex: 0 0 auto; }

		._img {
			margin-right: 14px;
			width: 19px;
			height: 27px; } }

	&__nav {
		display: flex;
		flex-direction: column;
		align-items: flex-start;

		&-item {
			color: #706a88;
			font-size: 15px;
			padding: 5px 0;
			margin: 5px 0;

			@include link;

			&.active {
				color: $blue-dark; } } }

	&__actions {
		display: flex;
		align-items: center;

		& > * {
			flex: 0 0 auto; } }

	&__user {
		@include link;

		display: flex;
		align-items: center;

		& > * {
			flex: 0 0 auto;

			color: #221849;
			font-size: 15px; }

		._img {
			margin-left: 10px;

			pointer-events: none;
			opacity: 0;
			transition: $tr; } }

	.open--mobile-menu & {
		opacity: 1;
		pointer-events: auto; } }
