html,
body {
	font-family: Montserrat;
	margin: 0;
	padding: 0;
	font-weight: 500;
	font-size: 15px;
	font-size: 16px;
	line-height: calc(22/16);
	background-color: #fff;
	color: #4e466e; }

* {
	box-sizing: border-box; }

img,
svg {
	max-width: 100%;
	max-height: 100%; }

.overflow-hidden {
	overflow: hidden; }

.mainwrap {
	padding-top: 85px;
	padding-top: 80px;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	min-height: 100vh;

	@include media-breakpoint-down(md) {
		padding-top: 60px; }

	& > * {
		flex: 0 0 auto; }

	main {
		flex-grow: 1; } }

a {
	text-decoration: none;
	cursor: pointer;
	&:hover {
		transition: 0.2s;
		color: #8667ff; } }

.breadcrumb {
	margin: 50px 0;

	.breadcrumb-item {
		a {
			color: $blue-dark;
			transition: $tr;

			&:hover {
				color: $blue; } } }

	@include media-breakpoint-down(lg) {
		margin: 30px 0; } }

.nav {
	&-tabs {
		border-bottom: none;
		font-size: 15px;
		font-weight: 500;

		@include media-breakpoint-down(sm) {

			font-size: 10px;
			line-height: 15px; }

		.nav-item {}

		.nav-link {
			height: 57px;
			border: 1px solid #e0e0e0;
			border-width: 0 0 1px 0 !important;
			background: none !important;
			position: relative;
			color: #706a88;
			font-weight: 500;
			font-size: 15px;
			@include media-breakpoint-down(sm) {

				font-size: 10px;
				line-height: 15px; }

			&.active {
				color: $blue-dark;

				&::before {
					border-bottom: 7px solid $blue;
					left: 0;
					bottom: 0;
					right: 0;
					content: "";
					position: absolute; } } } } }

.modal {
	&-open {
		.mainwrap {
			filter: blur(15px); } }

	&-backdrop {
		background: linear-gradient(180deg, rgba(33, 150, 243, 0.1) 0%, rgba(33, 150, 243, 0.1) 100%); }

	&-dialog {}

	&-content {
		border: none;
		box-shadow: 0 25px 25px rgba(36, 26, 75, 0.03);
		border-radius: 10px; }

	&-header {}

	&-body {
		@include min(1200) {
			padding: 50px 80px; }

		&>.btn-close {
			opacity: 1;
			position: absolute;
			right: -25px;
			top: -25px;
			background: transparent url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%230085ff'%3e%3cpath d='M.293.293a1 1 0 011.414 0L8 6.586 14.293.293a1 1 0 111.414 1.414L9.414 8l6.293 6.293a1 1 0 01-1.414 1.414L8 9.414l-6.293 6.293a1 1 0 01-1.414-1.414L6.586 8 .293 1.707a1 1 0 010-1.414z'/%3e%3c/svg%3e") (center / 1em) auto no-repeat; } }

	&-footer {} }

$hr: #cdc8e3;

#order-partner {
	.form-label {
		margin-top: 20px; }

	textarea {
		@include min(992) {
			height: 357px; } } }

hr {
	border-color: $hr; }

.border-md-start {
	@include media-breakpoint-up(md) {
		border-left: 1px solid $hr; } }

.socials {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	margin-right: -10px;

	& > a {
		width: 29px;
		height: 29px;
		line-height: 0;
		border-radius: 50%;
		background-color: #565656;
		border: 6px solid #f1f1f1;
		display: flex;
		align-items: center;
		justify-content: center;
		position: relative;
		margin-right: 10px;

		img {
			max-width: none;
			max-height: none;
			position: absolute;
			z-index: 2;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			width: 20px;
			height: 20px; }

		&:hover {

			background-color: $purple; } } }

small {
	font-size: 12px; }
