.btn {
	text-decoration: none !important;
	cursor: pointer;
	max-width: 100%;
	display: inline-block;
	vertical-align: middle;
	position: relative;
	z-index: 1;
	min-height: 60px;
	padding: 20px 50px;
	// color: $white
	font-size: 16px;
	line-height: 22px;
	font-weight: 600;
	letter-spacing: normal;
	border-radius: 10px;
	font-family: Montserrat;
	outline: none;

	@include media-breakpoint-down(sm) {
		padding: 20px 30px; }
	// SIZES

	&-sm {

		min-height: 36px;
		padding: 10px 30px;
		font-size: 12px;
		line-height: 15px;
		font-weight: 600; }

	&-circle {
		border-radius: 50%;
		width: 60px;
		height: 60px;
		display: flex;
		align-items: center;
		justify-content: center;
		text-align: center;
		padding: 10px;
		&.btn-sm {
			padding: 5px;
			width: 36px;
			height: 36px; } }
	// DISABLED
	&-disabled {
		pointer-events: none;
		background-color: $gray !important; }

	// HOVERS
	// +hover
	// 	background-color: $purple
	// 	color: $white


	&.active {
		background-color: $orange; }

	// COLORS

	// &-outline-primary,
	// &-outline-secondary,
	// &-outline-success,
	// &-outline-info,
	// &-outline-warning,
	// &-outline-danger,
	// &-outline-light,
	// &-outline-dark
	// 	background-color: #fff



	&-light {
		color: rgba(36, 26, 75, 0.8); }
	&-outline-light {
		color: rgba(36, 26, 75, 0.8);
		border: 1px solid rgba(36, 26, 75, 0.3);
		@include hover {
			background-color: lighten($blue,40%); } }

	&-primary {
		box-shadow: 0 25px 40px rgba(33, 150, 243, 0.2); }

	&-primary,
	&-secondary,
	&-success {
		color: $white !important; }


	&-outline-primary,
	&-outline-secondary,
	&-outline-success {
		@include hover {
			color: $white !important; } }

	&-dark {
		color: $white;
		background-color: $dark; }

	&-white {
		background-color: $white;
		color: $dark; } }

.favorite {
	width: 31px;
	height: 31px;
	background-color: $gray-light;
	border-radius: 50%;
	border: none;
	text-decoration: none !important;
	cursor: pointer;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	vertical-align: middle;
	position: relative;
	transition: $tr;

	&::before,
	&::after {
		content: "";
		width: 20px;
		height: 16px;
		background-repeat: no-repeat;
		background-position: 50% 50%;
		display: block;
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		transition: $tr; }
	&::before {
		background-image: url("data:image/svg+xml,%0A%3Csvg xmlns='http://www.w3.org/2000/svg' width='19' height='16'%3E%3Cpath fill='%23565656' d='M18.65 4.765a4.694 4.694 0 0 1-1.383 3.34l-6.926 6.926a.804.804 0 0 1-1.142 0L2.274 8.106A4.694 4.694 0 0 1 .89 4.765a4.69 4.69 0 0 1 1.384-3.341A4.694 4.694 0 0 1 5.614.04a4.69 4.69 0 0 1 3.342 1.384l.814.814.814-.814A4.694 4.694 0 0 1 13.926.04a4.69 4.69 0 0 1 3.34 1.384 4.694 4.694 0 0 1 1.384 3.34zm-2.525 2.2a3.09 3.09 0 0 0 .91-2.2 3.09 3.09 0 0 0-.91-2.2 3.114 3.114 0 0 0-4.399 0l-1.385 1.386a.807.807 0 0 1-1.142 0L7.814 2.566a3.1 3.1 0 0 0-2.2-.91 3.1 3.1 0 0 0-2.198.91 3.09 3.09 0 0 0-.911 2.199c0 .83.323 1.612.91 2.2l6.355 6.354z'/%3E%3C/svg%3E"); }

	&::after {
		opacity: 0;
		background-image: url("data:image/svg+xml,%0A%3Csvg xmlns='http://www.w3.org/2000/svg' width='19' height='16'%3E%3Cpath fill='%23ffffff' d='M18.65 4.765a4.694 4.694 0 0 1-1.383 3.34l-6.926 6.926a.804.804 0 0 1-1.142 0L2.274 8.106A4.694 4.694 0 0 1 .89 4.765a4.69 4.69 0 0 1 1.384-3.341A4.694 4.694 0 0 1 5.614.04a4.69 4.69 0 0 1 3.342 1.384l.814.814.814-.814A4.694 4.694 0 0 1 13.926.04a4.69 4.69 0 0 1 3.34 1.384 4.694 4.694 0 0 1 1.384 3.34zm-2.525 2.2a3.09 3.09 0 0 0 .91-2.2 3.09 3.09 0 0 0-.91-2.2 3.114 3.114 0 0 0-4.399 0l-1.385 1.386a.807.807 0 0 1-1.142 0L7.814 2.566a3.1 3.1 0 0 0-2.2-.91 3.1 3.1 0 0 0-2.198.91 3.09 3.09 0 0 0-.911 2.199c0 .83.323 1.612.91 2.2l6.355 6.354z'/%3E%3C/svg%3E"); }

	&:hover {
		background-color: $purple;
		&::before {
			opacity: 0; }
		&::after {
			opacity: 1; } }
	&:active {
		transform: scale(.7);
		background-color: $blue;
		&::before {
			opacity: 0; }
		&::after {
			opacity: 1; } }


	&.active {
		background-color: $blue;
		&::before {
			opacity: 0; }
		&::after {
			opacity: 1; } } }

.btn-link {
	background: none;
	border: none;
	text-decoration: none;
	text-align: left;
	padding: 0;

	color: rgba(36, 26, 75, 0.8);
	font-size: 12px;
	font-weight: 500;
	line-height: 16.93px; }
