.baron__clipper {
	position: relative;
	overflow: hidden; }

.baron__scroller {
	overflow-y: scroll;
	-ms-overflow-style: none;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
	margin: 0;
	border: 0;
	padding: 0;
	width: 100%;
	height: 100%;
	-webkit-overflow-scrolling: touch;
	scrollbar-width: none;

	&::-webkit-scrollbar {
		width: 0;
		height: 0; } }

.baron__track {
	display: none;
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0; }

.baron {
	height: 100%;
	&._scrollbar {
		&>.baron__track,
		&>.baron__bar {
			display: block; } } }

.baron__free {
	position: absolute;
	z-index: 2;
	top: 0;
	bottom: 0;
	right: 0;
	width: 4px;
	border-radius: 2px;

	._scrollbar & {
		background-color: rgba($blue,.15); } }


.baron__bar {
	display: none;
	position: absolute;
	right: 0;
	z-index: 1;
	width: 4px;
	background: #999;
	background-color: $blue;
	border-radius: 2px;

	._scrollbar & {
		display: block; } }

.baron__control {
	display: none; }


.baron-row {
	position: relative;
	.baron__track {
		position: absolute;
		z-index: 3;
		top: auto;
		left: 0;
		right: 0;
		bottom: 0;
		height: 4px;
		border-radius: 5px;
		background: #f8f8f8;
		pointer-events: none; }
	.baron__bar {
		position: absolute;
		z-index: 1;
		height: 4px;
		border-radius: 3px;
		background: $main;
		opacity: 0;
		-webkit-transition: opacity .2s linear;
		transition: opacity .2s linear;
		pointer-events: auto;
		opacity: 1;
		cursor: pointer;
		transition: width 0.2s;

		&:hover,
		&:active {
			width: 10px; } } }


.baron._simple {
	position: relative;
	.baron__track {
		opacity: 0;
		position: absolute;
		z-index: 3;
		top: 0;
		bottom: 0;
		right: 5px;
		width: 4px;
		border-radius: 5px;
		background: #f8f8f8;
		pointer-events: none; }

	&._scrollbar .baron__track {
		opacity: 1; }

	.baron__bar {
		position: absolute;
		z-index: 1;
		width: 4px;
		border-radius: 3px;
		background: $main;
		opacity: 0;
		-webkit-transition: opacity .2s linear;
		transition: opacity .2s linear;
		pointer-events: auto;
		opacity: 1;
		cursor: pointer;
		transition: width 0.2s;

		&:hover,
		&:active {
			width: 10px; } } }
