.sort {
	color: #221849;
	font-size: 16px;
	line-height: 22.58px;
	font-weight: 500;
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	margin: 0 -10px;
	@include max(767) {
		font-size: 10px; }

	&__text {
		margin: 3px 10px; }

	&__label {
		margin: 3px 10px;

		&-text {
			color: rgba(36, 26, 75, 0.5);
			cursor: pointer;

			@include hover {
				color: $blue-dark; } }

		input:checked + &-text {
			color: $blue; } } }

label {
	display: block; }

.form-control {
	height: 48px;
	border-radius: 5px;
	border: 1px solid #cdc8e3;
	background-color: #ffffff;
	transition: $tr;
	padding: 13px 23px;
	color: #221849;
	font-size: 16px;
	font-weight: 500;

	&::placeholder {
		color: #cdc8e3; }

	&:focus,
	&:hover {
		border-color: #005798 !important;
		box-shadow: none; }

	& ~ .invalid-feedback,
	& ~ .valid-feedback {
		padding-left: 14px; } }

textarea.form-control {
	height: auto; }

.was-validated .form-control:valid,
.form-control.is-valid,
.was-validated .form-control:invalid,
.form-control.is-invalid {
	// background: #fff !important
	box-shadow: none !important;

 }	// padding-right: 12px

// checkbox / radio
.label-check {
	padding-top: 0;
	padding-left: 32px;
	cursor: pointer;
	position: relative;
	color: #706a88;
	font-size: 13px;
	font-weight: 500;
	line-height: 20px;

	&::before {
		content: "";
		position: absolute;
		left: 0;
		top: 0;
		width: 20px;
		height: 20px;
		border: 1px solid #cdc8e3;
		background-color: #ffffff; }

	&::after {
		content: "";
		position: absolute;
		left: 4px;
		top: 4px;
		width: 12px;
		height: 12px;
		box-shadow: 0 0 60px rgba(86, 212, 191, 0.2);
		background-color: $blue;
		transform: scale(0);
		opacity: 0.5;
		transition: 0.15s; }

	input[disabled] ~ & {
		&::before {
			border: 2px solid #eeeeee;
			background-color: #eeeeee; } }

	input:not([disabled]) ~ &:hover {
		&::after {
			transform: scale(1); } }

	input:checked ~ & {
		&::after {
			transform: scale(1);
			opacity: 1; } }

	input[type=checkbox] ~ & {
		&::before {
			border-radius: 5px; }

		&::after {
			border-radius: 3px; } }

	input[type=radio] ~ & {
		&::before {
			border-radius: 50%; }

		&::after {
			border-radius: 50%; } } }

.chosen-container {
	&.min-w-180 {
		min-width: 180px; }

	&.w-180 {
		width: 180px; }

	.chosen-results {
		li {
			color: rgba(36, 26, 75, 0.6);
			font-size: 16px;
			font-weight: 500;
			line-height: 22.58px;
			padding-left: 22px;
			padding-right: 22px;

			&.active-result {}

			&.result-selected {
				color: $blue-dark; } } }

	.chosen-single {
		height: 50px;
		border-radius: 5px;
		border: 1px solid #2196f3;
		border: 1px solid #cdc8e3;
		transition: border-color 0.15s;

		.was-validated select:invalid ~ & {
			border-color: #dc3545; }

		.was-validated select:valid ~ & {
			border-color: #27ae60; }

		&:focus {
			border-color: #005798 !important;
			box-shadow: none; }

		@include media-breakpoint-up(xl) {
			&:hover {
				border-color: #005798 !important;
				box-shadow: none; } } }

	&.chosen-with-drop {
		.chosen-single {
			border: 1px solid #2196f3;
			border-radius: 5px;

			.was-validated select:invalid ~ & {
				border-color: #dc3545; }

			.was-validated select:valid ~ & {
				border-color: #27ae60; } }

		.chosen-drop {
			margin-top: 8px;
			transition: border-color 0.15s, border-top-color 0s;
			border: 1px solid #2196f3;
			border-radius: 5px;
			box-shadow: 0 6px 6px rgba(0, 0, 0, 0.05);

			.was-validated select:invalid ~ & {
				border-color: #dc3545;
				border-top-color: transparent; }

			.was-validated select:valid ~ & {
				border-color: #27ae60;
				border-top-color: transparent; } } }

	.was-validated select:invalid ~ &:hover {
		.chosen-single {
			border-color: #005798; }

		&.chosen-with-drop .chosen-drop {
			border-color: #005798;
			border-top-color: transparent; } } }

.form-datepicker {
	&[readonly] {
		background-color: #fff; } }

.form-control-required {
	font-weight: 400;
	font-size: 16px;
	line-height: 22px;
	color: #eb5757;
	position: absolute;
	transform: translate(-10px, 10px); }

.form-control-label {
	position: relative;
	width: 100%;

	input {
		opacity: 0;
		position: absolute;
		z-index: 2;
		height: 44px;
		left: 0;
		right: 0;
		top: 0;
		min-width: 100%;
		cursor: pointer; }

	&__area {
		height: 44px;
		border-radius: 5px;
		border: 2px dashed #eeeeee;
		background-color: #ffffff;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 10px 30px 8px;
		cursor: pointer;
		font-weight: 400;
		font-size: 16px;
		line-height: 16px;
		color: #1b2026;
		transition: 0.2s;
		fill: rgba(33, 150, 243, 0.6);

		@include media-breakpoint-down(sm) {
			padding: 10px 7px 8px;
			font-size: 12px;
			line-height: 11px; }


		@include media-breakpoint-up(xl) {
			.MultiFile-wrap:hover ~ & {
				border-color: $blue; } }


		svg {
			flex: 0 0 auto;
			width: 17px;
			height: 17px;
			margin-right: 7px;
			margin-top: -3px; } }

	&__text {
		margin-top: 10px;
		font-size: 12px;
		line-height: 12px;
		color: #b2b2b2; }

	.MultiFile-remove {
		font-size: 0;
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;

		&:hover {
			&::before {
				transform: rotate(45deg) scale(1.15); }

			&::after {
				transform: rotate(-45deg) scale(1.15); }

			& ~ * .MultiFile-title {
				color: #eb5757; } }

		&::before,
		&::after {
			transition: 0.2s;
			content: "";
			position: absolute;
			left: 35px;
			top: calc( 50% - 2px );
			height: 2px;
			width: 18px;
			border-radius: 2px;
			background-color: #eb5757; }

		&::before {
			transform: rotate(45deg); }

		&::after {
			transform: rotate(-45deg); } }

	&__list > .MultiFile-label {
		position: relative;
		padding: 10px;
		padding-left: 35px + 22px;
		font-size: 16px;
		line-height: 22px;
		color: #1b2026;
		border-bottom: 2px solid #eeeeee;

		.MultiFile-title {
			transition: 0.2s; } } }

.search {
	position: relative;
	margin: 20px 0;

	.form-control {
		padding-right: 80px; }

	&-btn {
		position: absolute;
		right: 0;
		top: 0;
		display: flex;
		align-items: center;
		justify-content: center;
		text-align: center;
		height: 48px;
		padding: 10px 30px;
		background: none;
		border: none;
		outline: none; } }

.form-control:focus, .form-control:hover {
	border-color: $blue !important; }
.form-label {
	&--small {
		color: rgba(36, 26, 75, 0.5);
		font-size: 12px;
		font-weight: 500;
		line-height: 19.33px; } }
input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
	width: 20px;
	display: none; }

input[type=number] {
	-moz-appearance: textfield; }

.quantity {
	position: relative;

	input {
		padding-right: 50px; }

	&-nav {
		right: 1px;
		top: 1px;
		bottom: 1px;
		position: absolute;
		width: 30px; }

	&-button {
		position: relative;
		cursor: pointer;
		border-left: 1px solid #eee;
		width: 100%;
		font-size: 16px;
		line-height: 26px;
		user-select: none;
		text-align: center;
		transition: $tr;

		&:hover {
			color: $blue; }

		&.quantity-up {
			position: absolute;
			height: 50%;
			top: 0;
			border-bottom: 1px solid #eee; }

		&.quantity-down {
			line-height: 20px;
			position: absolute;
			bottom: 0px;
			height: 50%; } } }

.option {
	margin: 10px 0;
	display: flex;
	align-items: center;

	label {
		display: inline-block; }

	.label-check {
		flex: 0 0 auto;
		height: 20px; }

	&__input-text {
		font-family: Montserrat;
		color: rgba(36, 26, 75, 0.8);
		font-size: 12px;
		font-weight: 500;
		line-height: 16.93px;
		padding: 5px 0;
		border: none;
		outline: none;
		border-bottom: 1px solid #cdc8e3;
		flex: 1 1 auto;

		&:focus {
			border-color: $blue; }

		@include hover {
			border-color: $blue; } }

	&__remove {
		line-height: 0;
		flex: 0 0 auto;
		margin-left: 10px;
		width: 9px;
		height: 9px;
		fill: #8887a6;
		cursor: pointer;

		@include hover {
			fill: $blue;
			transform: scale(1.5); } } }

.form-switch {

	.form-check-input {
		margin-top: 2px; }
	&--big {
		padding-left: 70px;
		min-height: 36px;

		.form-check-input {
			margin: 0;
			width: 75px;
			height: 40px;
			width: 70px;
			height: 36px;
			margin-left: -70px; }

		.form-check-label {
			padding-left: 15px; } } }

.min-width-180 {
	min-width: 180px; }
