.production {
  position: relative;
  padding-top: 75px;
  padding-bottom: 400px;
  min-height: 40vw;
  background-image: url(../img/monument-bg.svg);
  background-position: center 100%;
  background-size: cover;
  &::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100px;
    top: -140px;
    background: #ECF5FC; }

  &__content {
    padding-right: 40%; }

  &__text {
    color: $blue-dark;
    font-size: 20px;
    line-height: 28.22px; }

  &__action {
    position: relative;
    margin-top: 50px;

    .btn {
      margin-right: 10px; } }

  &__icon {
    position: absolute;
    top: 20px;
    left: auto; }

  &__info {
    color: rgba(36, 26, 75, 0.8);
    font-size: 16px;
    line-height: 22.58px;
    font-weight: 500;
    margin-bottom: 20px; }

  @include media-breakpoint-down(lg) {
    &__content {
      padding-right: 0; } }

  @include media-breakpoint-down(md) {
    padding-bottom: 300px; }

  @include media-breakpoint-down(sm) {
    padding-top: 40px;
    &::before {
      top: -200px; }
    ._info {
      font-size: 13px; }
    br {
      display: none; } } }

.prices {
  padding-top: 80px;
  overflow: hidden;
  position: relative;
  z-index: 1;
  text-align: center;
  &::before {
    content: '';
    position: absolute;
    z-index: -1;
    width: 2137px;
    height: 2137px;
    border-radius: 50%;
    bottom: 15px;
    left: 50%;
    transform: translateX(-50%);
    background: #1C468A;

    @include media-breakpoint-up(xxl) {
      width: 150%; } }

  .title-1 {
    color: #ffffff; }

  .title-3 {
    color: rgba(255, 255, 255, 0.7); }

  &-guarantees {
    display: flex;
    justify-content: center;
    align-items: baseline; }

  &-guarantee {
    align-items: center;
    max-width: 270px;
    min-width: 270px;
    display: flex;
    flex-direction: column;
    margin: 0 30px;

    &__icon {
      max-width: 95px;
      max-height: 62px;
      margin-bottom: 25px; }

    &__title {
      font-weight: 600;
      font-size: 20px;
      line-height: 30px;
      color: #FFFFFF; }

    &__text {
      font-weight: 500;
      font-size: 16px;
      line-height: 23px;
      color: rgba(255, 255, 255, 0.7); } }

  @include media-breakpoint-down(lg) {
    &-guarantees {
      flex-wrap: wrap; }
    &-guarantee {
      margin: 0 0 30px; } }

  @include media-breakpoint-down(md) {
    &-guarantee {
      &__icon {
        margin-bottom: 15px; } }
    br {
      display: none; } } }

.monuments-catalog {
  .nav-tabs {
    justify-content: center; }

  .product-banner {
    margin: 100px 0;
    &__img {
      bottom: 35px; }
    &__offers {
      margin-top: 10px;
      display: flex;
      align-items: center; }
    &__icon {
      margin-right: 20px;
      flex: 0 0 auto; }
    &__offer {
      font-weight: 500;
      font-size: 16px;
      line-height: 23px;
      color: rgba(255, 255, 255, 0.7); } }

  @include media-breakpoint-down(xl) {
    br {
      display: none; } }

  @include media-breakpoint-down(lg) {
    .product-banner {
      margin-top: 80px; }
    .nav-tabs {
      justify-content: start; } }

  @include media-breakpoint-down(md) {
    .product-banner {
      margin: 40px 0; } } }

.steps {
  &__item {
    max-width: 230px;
    min-width: 230px;
    flex-direction: column;
    align-items: flex-start;
    margin: 0 40px 30px; }
  &__icon {}
  &__title {
    font-weight: 600;
    font-size: 20px;
    line-height: 30px; }
  &__text {
    font-weight: 500;
    font-size: 16px;
    line-height: 23px;
    color: rgba(36, 26, 75, 0.8); }

  &__order {
    max-width: 1035px;
    padding: 35px 30px;
    align-items: center;
    background: rgba(236, 245, 252, 0.7);
    border-radius: 15px;
    &-icon {
      width: 86px;
      height: 36px; } }
  &__sale {
    max-width: 400px; }

  @include media-breakpoint-down(xl) {
    &__sale {
      max-width: 300px; } }

  @include media-breakpoint-down(md) {
    &__item {
      text-align: center;
      align-items: center; }
    &__order {
      flex-direction: column; }
    &__sale {
      text-align: center; } } }

.work-examples {
  position: relative;
  z-index: 1;
  &::before {
    content: '';
    position: absolute;
    z-index: -1;
    top: 50%;
    width: 100%;
    height: 655px;
    background: #ECF5FC;
    transform: matrix(1, 0, 0, -1, 0, 0); }

  @include media-breakpoint-down(lg) {
    &::before {
      top: 60%; } }

  @include media-breakpoint-down(md) {
    &::before {
      top: 50%; } } }

.examples {
  &-slider {
    max-width: 1035px;
    margin: 0 auto;
    box-shadow: 0px 25px 25px rgba(36, 26, 75, 0.03);
    &__arrow {
      bottom: 30px;
      position: absolute;
      z-index: 2;
      cursor: pointer;
      &-prev {
        left: 10%; }
      &-next {
        right: 77%;
        transform: rotate(180deg); } } }
  &-slide {
    padding: 45px 50px 45px 105px;
    background: #FFFFFF;
    border: 1px solid rgba(36, 26, 75, 0.1);
    border-radius: 15px;
    &:focus {
      outline: none; }
    &__info {
      position: relative;
      display: flex;
      flex-direction: column;
      width: 390px;
      min-height: 260px;
      margin-bottom: 70px;
      margin-right: 80px;
      word-break: break-word; }
    &__text {
      font-weight: 500;
      font-size: 16px;
      line-height: 23px;
      color: rgba(36, 26, 75, 0.7); }
    &__img {
      max-width: 415px;
      height: 333px;
      object-fit: cover;
      background: rgba(36, 26, 75, 0.05);
      border-radius: 15px; }
    &__counter {
      position: absolute;
      bottom: -75px;
      right: 0;
      z-index: 2;
      color: rgba(36, 26, 75, 0.7); } }
  @include media-breakpoint-down(lg) {
    &-slider {
      &__arrow {
        &-prev {
          left: 7%; }
        &-next {
          right: 75%; } } }
    &-slide {
      padding: 45px;
      flex-wrap: wrap;
      &__info {
        min-height: auto;
        padding-bottom: 30px;
        margin-right: 0;
        width: 100%; }
      &__img {
        order: -1;
        max-width: 100%;
        height: auto;
        margin-bottom: 40px; } } }

  @include media-breakpoint-down(md) {
    &-slider {
      &__arrow {
        bottom: 20px;
        &-prev {
          left: 6%; }
        &-next {
          right: 70%; } } }
    &-slide {
      padding: 30px;
      &__img {
        margin-bottom: 15px; }
      &__counter {
        bottom: -70px; } } }

  @include media-breakpoint-down(sm) {
    &-slider {
      &__arrow {
        bottom: 13px;
        &-next {
          right: 8%; } } }
    &-slide {
      padding: 10px;
      &__counter {
        bottom: -58px;
        right: 45%; } } } }
